.contact_left {
    border: 1px solid var(--border-color);
    border-radius: 1.2rem;
    padding: 0.3rem;
    height: 97vh;
    width: 100%;
    gap: 0.5rem;
    display: flex;
    flex-direction: column;
}

.contact_left-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 1px solid var(--border-color);
    border-radius: 1rem;
    padding: 0.5rem;
    gap: 2rem;
    height: 54vh;
    width: 100%;
}

.contact_left-container h1 {
    font-size: 4rem;
    text-transform: capitalize;
    color: var(--title-color);
    font-family: 'Lobster';
    letter-spacing: 0.2rem;
    position: relative;
    top: 2rem;
}

.contact_left-container span {
    font-size: 1.1rem;
    text-transform: capitalize;
    color: var(--title-color);
    font-family: 'GeneralSans-Regular';
    letter-spacing: 0.2rem;
    width: 80%;
}

.contact__left-bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 1px solid var(--border-color);
    border-radius: 1rem;
    padding: 0.5rem;
    gap: 2rem;
    height: 30.5vh;
    width: 100%;
}
.smallRight__cards-contact {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    height: 40%;
    padding-left: 0.2rem;
}

.smallRight__card-contact {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%;
    height: 100%;
    border-top: 0.5px solid var(--border-color);
    padding: 0rem 0;
}

.smallRight__card-contact:last-child {
    padding-bottom: 0;
}
.smallRight__card-contact:first-child {
    border-top: 0;
}

.smallRight__card-end-contact {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}
.smallRight__card-contact h1 {
    font-size: 0.9rem;
    color: var(--title-color);
    font-family: 'GeneralSans-Regular';
    letter-spacing: 0.1rem;
    text-transform: uppercase;
}

.smallRight__card-end-contact p {
    font-size: 0.9rem;
    color: var(--accent-color);
    font-family: 'GeneralSans-Light';
    letter-spacing: 0.1rem;
}
.smallRight__card-end-contact a {
    font-size: 0.9rem;
    color: var(--accent-color);
    font-family: 'GeneralSans-Light';
    letter-spacing: 0.1rem;
    padding-bottom: 1rem;
}

.contact__footer {
    display: flex;
    justify-content: space-between;
    padding: 1rem;
    align-items: center;
    width: 100%;
    position: relative;
    bottom: 0rem;
    border: 1px solid var(--border-color);
    height: 10vh;
    border-radius: 1rem;
    cursor: pointer;
}

.contact__footer span {
    font-size: 0.8rem;
    text-transform: capitalize;
    color: var(--accent-color);
    font-family: 'GeneralSans-Light';
    letter-spacing: 0.1rem;
}
.contact__footer span a {
    color: var(--accent-color);
}

.contact__footer p {
    font-size: 1rem;
    text-transform: capitalize;
    color: var(--accent-color);
    font-family: 'GeneralSans-Light';
    letter-spacing: 0.1rem;
}

.smallRight__card {

    width: 100%;
    height: 60%;
    padding: 0.2rem 0;
}

@media (max-width: 469px) {
    .contact_left {
        height: 110vh;
    }
    .contact_left-container {
        height: 80vh;
        width: 100%;
    }
    .contact_left-container span {
        font-size: 1rem;
        letter-spacing: normal;
    }
    .contact__left-bottom {
        height: 40vh;
    }
    .contact__footer {
        height: 10vh;
    }
}

@media (max-width: 380px) {
    .contact_left {
        height: 107vh;
    }
    .contact_left-container  {
        height: 55vh;
    }
}

@media (min-width: 1680px) {    

    .smallRight__card-end-contact {
        padding-top: 1rem;
    }
    .smallRight__card h1 {
        padding-bottom: 0.5rem;
    }
    .smallRight__card-contact h1:first-child {
        padding-top: 1rem;
    }
    
}